import React from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import '../Manager/EditTeam.css'
const EditTeam = () => {
    let IncomingTeam = JSON.parse(localStorage.getItem('TeamStock'))
    const handleTeamDis = () => document.getElementById("addMember").style.display = "flex";
    const handleTeamAdd = () => {
        let f = new Date();
        let temp = {
            id: f.getTime(),
            First_name: document.getElementById("firstname").value,
            Second_name: document.getElementById("secondname").value,
            Avatar: "fas fa-user-tie",
            DOB: document.getElementById("dob").value,
            Gender: document.getElementById("gender").value,
            Exp: document.getElementById("experience").value,
            Password: document.getElementById("pass").value,
            img:"https://www.sasscaffold.com.au/wp-content/uploads/2019/10/pngtree-business-male-user-avatar-vector-png-image_1511454.jpg"
        }
        IncomingTeam.push(temp);
        console.log(IncomingTeam)
        localStorage.setItem("TeamStock", JSON.stringify(IncomingTeam));
        console.log("local", JSON.stringify(IncomingTeam))
        settotallitem(totallitem + 1);
        document.getElementById('addMember').style.display = "none"
    }
    const [totallitem, settotallitem] = useState(IncomingTeam.length)
    return (
        <div id='maindiv'>
            <nav id='editTeamNav'>
                <div>
                    <h1 id="welcome">Welcome</h1>
                    <h3 id='name'>Shubham Upadhyay</h3>
                </div>
                <div id="desdiv">
                    <h3 id="des">Store Manager</h3>
                    <i class="fas fa-user-tie"></i>
                </div>
            </nav>
            <div id='contentArea'>
                <div id="head">
                    <h2>Team Management</h2>
                    <button onClick={() => handleTeamDis()} class='buttonClassaz'>ADD MEMBER</button>
                </div>
                <div id="addMember">
                    <input type={Text} placeholder='First Name' class='addform' id='firstname'></input>
                    <input type={Text} placeholder='Second Name' class='addform' id='secondname'></input>
                    <input type={Text} placeholder='Gender' class='addform' id='gender'></input>
                    <input type={Text} placeholder='Date of Birth' class='addform' id='dob'></input>
                    <input type={Text} placeholder='Experience' class='addform' id='experience'></input>
                    <input type={'password'} placeholder='Password' class='addform' id='pass'></input>
                    <button onClick={() => handleTeamAdd()} class='buttonClass2'>ADD MEMBER</button>
                </div>
                <div id="TeamHolder">

                    {IncomingTeam.map(item => <Team {...item}></Team>)}


                </div>
            </div>

        </div>
    );
}

export default EditTeam;
const Team = ({ id,First_name, Second_name, Gender, Exp, img, Password, DOB }) => {
    let linky = '/CRUDteam' +'?'+ 'id=' +id
    return (
       <Link to={linky}>
           <div id="TeamDetail" style={{width:"30%"}}>
            <div id="teamImg">
                <img src={img} />
            </div>
            <div id="memdetail">
                <h3><span id="firstName">{First_name}</span><span>{Second_name}</span></h3>
                <h4>{Gender}</h4>
                <h4>DOB: {DOB}</h4>
                <h4>Exp Years {Exp} </h4>
                <h4>{Password}</h4>
            </div>
        </div>
        </Link>
    );
}