import React from "react";
import { useState } from "react";
import { Redirect } from "react-router";
import "../logins/index.css";
const Logins = () => {
  let admin = {
    user: "test-admin",
    password: "test-admin",
  };
  let salesExecutive = {
    First_name: "test-sales",
    Password: "test-sales",
    Avatar: "fas fa-user-tie",
DOB: "v",
Exp: "v",
Gender: "v",
Second_name: "v",
id: 1631540396845,
img: "https://www.sasscaffold.com.au/wp-content/uploads/2019/10/pngtree-business-male-user-avatar-vector-png-image_1511454.jpg"
  }
  let incoming;
  const andy = () => {
    if (JSON.parse(localStorage.getItem("TeamStock")))
      incoming = JSON.parse(localStorage.getItem("TeamStock"));
    else {
      let sales_executive = [];
      sales_executive.push(salesExecutive);
      localStorage.setItem(
        "TeamStock",
        JSON.stringify(sales_executive)
      );
    }
  }
  andy();
  console.log(incoming);
  const handleStoreManager = () => {
    let y = document.getElementById("storeManagerid").value;
    let y1 = document.getElementById("storeManagerpass").value;
    console.log(y, y1);
    if (y === "" || y1 === "") alert("please fill all the fields");
    else {
      if (y.toLowerCase() === admin.user && y1 === admin.password)
        setstore(true);
      else alert("Either username or password is wrong");
    }
  };
  const handleSalesExecutive = () => {
    console.log(incoming)
    let y = document.getElementById("salesExecutiveid").value;
    let y1 = document.getElementById("salesExecutivepass").value;
    if (y == "" || y1 == "") alert("please fill all the fields");
    else {
      for (let i = 0; i < incoming.length; i++) {
        console.log("ch", incoming[i].First_name, y, incoming[i].Password, y1)
        if (incoming[i].First_name == y.toLowerCase() && y1 == incoming[i].Password) {
          setsales(true);
          break;
        } else {
          alert("Either username or password is wrong");
        }
      }
    }
  };
  const [store, setstore] = useState(false);
  const [sales, setsales] = useState(false);
  return (
    <div className="homeLogin">
      {store && <Redirect to="/storemanager" />}
      {sales && <Redirect to="/sales" />}
      <div className="login-page">
        <div className="form">
          <div className="login">
            <div className="login-header">
              <h3>STORE MANAGER LOGIN</h3>
              <p>Please enter your credentials to login.</p>
            </div>
          </div>
          <form className="login-form">
            <input id="storeManagerid" type="text" placeholder="username" />
            <input
              id="storeManagerpass"
              type="password"
              placeholder="password"
            />
            <button onClick={() => handleStoreManager()}>login</button>
          </form>
        </div>
      </div>
      <div className="login-page">
        <div className="form">
          <div className="login">
            <div className="login-header">
              <h3>SALES EXECUTIVE LOGIN</h3>
              <p>Please enter your credentials to login.</p>
            </div>
          </div>
          <form className="login-form">
            <input id="salesExecutiveid" type="text" placeholder="username" />
            <input
              id="salesExecutivepass"
              type="password"
              placeholder="password"
            />
            <button onClick={() => handleSalesExecutive()}>login</button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Logins;
