import React from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import '../Manager/manageinventory.css'
const InvManage = () => {
    let IncomingMedicine = JSON.parse(localStorage.getItem('MedicineStock'))
    const handleADD = () => {
        document.getElementById('additem').style.display = "flex"
    }
    const handleADDgo = () => {
        let f=new Date();
        let temp={
            id:f.getTime(),
            Name:document.getElementById("addMediname").value,
            MafName:document.getElementById("addMediManufacturer").value,
            Price:document.getElementById("addMediPrice").value,
            Discount:document.getElementById("addMediDiscount").value,
            Stock:document.getElementById("addMediStock").value
        }
        IncomingMedicine.push(temp);
        console.log(IncomingMedicine)
        localStorage.setItem("MedicineStock",JSON.stringify(IncomingMedicine));
        console.log("local",JSON.stringify(IncomingMedicine))

        settotallitem(totallitem+1);
        document.getElementById('additem').style.display = "none"
    }
    const [totallitem, settotallitem] = useState(IncomingMedicine.length)
    return (
        <div id='maindiv'>
            <nav id='mnginvnav'>
                <div>
                    <h1 id="welcome">Welcome</h1>
                    <h3 id='name'>Shubham Upadhyay</h3>
                </div>
                <div id="desdiv">
                    <h3 id="des">Store Manager</h3>
                    <i class="fas fa-user-tie"></i>
                </div>
            </nav>

            <div id='contentArea'>
                <div id="head">
                    <h2>Inventory Management</h2>
                    <button class='buttonClassbz' onClick={
                        () => { handleADD() }
                    }>ADD ITEM</button>
                </div>
                <div id="additem">
                    <input id="addMediname" type={Text} placeholder='Name' class='addform'></input>
                    <input id="addMediManufacturer"  type={Text} placeholder='Manufacturer' class='addform'></input>
                    <input id="addMediPrice" type={Text} placeholder='Price' class='addform'></input>
                    <input id="addMediDiscount" type={Text} placeholder='Discount' class='addform'></input>
                    <input id="addMediStock" type={Text} placeholder='Stock' class='addform'></input>
                    <button class='buttonClass1' onClick={
                        () => { handleADDgo() }}>ADD ITEM</button>
                </div>
                <div id="medHolder">

                    {IncomingMedicine.map(item => <MedSetting {...item}></MedSetting>)}


                </div>
            </div>
        </div>
    );
}

export default InvManage;
const MedSetting = ({id, Name, MafName, Price, Discount, Stock }) => {
    let linky = '/CRUDmed' +'?'+ 'id=' +id
    return (
        <Link to={linky} style={{width:'22%',margin:"0 15px"}}>
        <div id="itemDetail">
            <h2>{Name}</h2>
            <h3>{MafName}</h3>
            <h3>Rs <span>{Price}</span>,<span class="margining" id="discount">{Discount}<span>% off</span></span></h3>
            <p>Stock <span>{Stock}</span></p>

        </div>
        </Link>
    );
}
